@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;900&display=swap");
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

img {
  max-width: 100%;
  width: 100%;
  display: flex;
  object-fit: cover;
  object-position: center center;
}

a {
  text-decoration: none;
  color: inherit;
  font-size: inherit;
}

video {
  object-fit: cover;
  object-position: center center;
}

p a {
  display: inline;
}

li {
  list-style-type: none;
}

table,
tr,
td {
  border-collapse: collapse;
  border-spacing: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  hyphens: auto;
  font-weight: inherit;
  font-size: inherit;
}

::selection {
  background-color: #000000;
  color: #ececec;
}

form input,
form textarea,
form select {
  font-family: inherit;
  font-size: inherit;
  hyphens: auto;
  background-color: transparent;
  display: flex;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 100%;
  line-height: 1.4em;
  min-height: 100vh;
  background-color: #ececec;
  hyphens: auto;
  font-smooth: always;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

p {
  color: #000000;
  line-height: 1;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
  line-height: 1;
}

h2 {
  font-size: 3.6rem;
  font-weight: 700;
  line-height: 1;
}

h3 {
  font-size: 3rem;
  font-weight: 700;
  line-height: 1;
}

.bg-verde {
  background-color: #4cb8b3;
}

.bg-amarillo {
  background-color: #fef46d;
}

.bg-rosa {
  background-color: #f53756;
}

.header {
  background-color: #4cb8b3;
}
.header .contenido-header {
  padding: 2.5rem 0;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  color: #ececec;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header.fijo {
  position: fixed;
  top: 0;
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem #444444;
}

.navegacion-principal {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navegacion-principal a {
  color: #ececec;
  font-size: 2rem;
  margin: 0 1rem;
  transition: all 0.2s ease-out;
}
.navegacion-principal a:hover {
  color: #fef46d;
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+62,ffffff+100&0.8+0,1+100 */
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.92) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(53, 6, 92, 0.92) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.92) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#ffffff",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
.video video {
  display: block;
  width: 100%;
}

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}
.contenido-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #ececec;
  font-size: 7rem;
  line-height: 7rem;
}
.contenido-video h2 span {
  background-color: #f53756;
  display: inline;
  padding: 1rem;
  font-size: 2rem;
  line-height: 2rem;
  height: 50px;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenido-festival h2 {
  font-size: 3rem;
  line-height: 1;
}
.contenido-festival .fecha {
  color: #f53756;
}
.contenido-festival .texto {
  line-height: 1.5;
}

.line-up {
  padding: 5rem 0;
}
.line-up h3,
.line-up p {
  text-align: center;
  color: #ececec;
}
.line-up .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .contenido-escenarios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.contenido-escenarios .nombre-escenario {
  color: #000000;
  font-size: 2rem;
  font-weight: 300;
}
.contenido-escenarios .nombre-escenario::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
}
.contenido-escenarios .escenario {
  padding: 2.5rem 0;
  margin: 2.5rem 0;
}
@media (min-width: 768px) {
  .contenido-escenarios .escenario {
    margin: 0;
  }
}

.edm .nombre-escenario::after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario::after {
  background-image: url(../img/icono_rock.png);
}

.calendario li {
  padding: 2.5rem;
  font-weight: 700;
  line-height: 1;
}
.calendario li span {
  font-weight: 400;
}

.bg-verde .calendario li:nth-child(odd) {
  background-color: #3b9691;
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #fef03a;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2.5rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2.5rem;
  }
}

.lightbox {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  display: grid;
  place-items: center;
}
.lightbox img {
  width: initial;
}
.lightbox .btn-cerrar {
  background-color: #ececec;
  width: 5rem;
  height: 5rem;
  font-weight: 400;
  font-size: 2.5rem;
  line-height: 1;
  border-radius: 50%;
  display: grid;
  place-items: center;
}

body.fijar-body {
  overflow: hidden;
}

.entradas {
  padding: 5rem 0;
}
.entradas h3 {
  text-align: center;
  color: #ececec;
}
.entradas .contenido-entradas {
  margin-top: 2.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
.entradas .plan p {
  color: #ececec;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
}

.pase {
  width: 100%;
}
.pase thead {
  background-color: #f53756;
}
.pase thead th {
  padding: 1.25rem;
  color: #ececec;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2rem;
}
.pase tbody {
  background-color: #ececec;
}
.pase tbody td {
  padding: 1.25rem;
  border-bottom: solid 1px #e1e1e1;
}
.pase tbody tr:last-of-type td {
  border: none;
}
.pase tfoot {
  background-color: #752f97;
}
.pase tfoot td {
  padding: 1.25rem;
  text-align: center;
  font-size: 3rem;
  color: #ececec;
}

.footer {
  text-align: center;
  padding: 1.25rem;
}
.footer p {
  color: #ececec;
}